.tasksColumns {
  display: flex;
  height: 550px;
  overflow-y: scroll;
  height: '100%';
}

.taskCol {
  position: relative;
  width: 33%;
  height: 100%;
}

.taskColTitle {
  display: flex;
  position: sticky;
  top: 0;
  margin-top: -20px;
  margin-bottom: 20px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.777);
  justify-content: center;
  align-items: center;
  width: 100%;
}

@media (max-width: 800px) {
  .tasksColumns {
    flex-direction: column;
    width: 100%;
    height: 2000px;
  }
  .taskCol {
    width: 100%;
  }
}
